.banner{
    background-size: cover;
    height: 600px;
    justify-content: baseline;
}
.content{
    max-width: 30rem;
    padding-top: 200px;
    height: 190px;
    padding-left: 20px;
    margin-left: 10px;
}
.button{
    color: white;
    outline: none;
    border: none;
    margin-right: 1rem;
    border-radius: 5px;
    font-weight: 700;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51,0.5);
    cursor: pointer;
    
}
.button:hover{
    background-color:#e6e6e6;
    color: black;
    
}
.discription{
    width: 45rem;
    height: 80px;
    font-weight: 500;
    color: white;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    max-width: 360px;
}
.title{
    color: white;
    font-size: 3rem;
    font-weight: 800;
    
}
.fade{
    margin-top: 82px;
    height: 8rem;
    background-image:linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111) ;
   
}