.row{
    margin-left: 20px;
    color: #fff;
    max-width: 100%;
    
}
.posters{
    
    display: flex;
    overflow-x: auto;
    overflow-y: hidden;
    padding-top: 5px;
    padding-left: 20px;
    margin-top: -15px;
    will-change: transform;
    
    
}

.poster{
    max-height: 150px;
    margin-right: 10px;
    padding-top: 15px;
    padding-bottom: 15px;
    /* will-change: transform; */
    
    

}

.poster:hover{
    transform: scale(1.2);
    transition: 0.1s;
    
   
}

.posters::-webkit-scrollbar{
    display: none;
}

.row-title{
    padding-left: 20px;
    font-size: large;
}
.posters:hover{
    transform: scale(1.1);
    transition:0.1s
   
}


