.navbar {
    position: fixed;
    top: 0;
    height: 3rem;
    width: 100%;
    display: flex;
    padding: 20px;
    justify-content: space-between;
    background-image: linear-gradient(0deg, transparent, rgba(37, 37, 37, 0.8), #111);
    font-size: 1rem;
  }
  
  .logo {
    position: fixed;
    left: 20px;
    width: 80px;
  }
  
  .avatar {
    position: fixed;
    right: 20px;
    width: 20px;
  }
  
  .menu {
    margin-left: 100px;
    display: flex;
    justify-content: space-between;
    
    text-align: center;
    
  }
  
  .menu a {
    margin-left:50px;
    text-decoration: none;
    color:white

  }
  